import React, { useState, useEffect, useContext } from 'react';
import Post from '../services/Post';
import PostCreator from '../services/PostForm';
import { FaUserCircle } from 'react-icons/fa';
import '../styles/news.scss';
import { UserContext } from '../context/UserContext';

function News() {
  const API = process.env.REACT_APP_API_URL;
  const [posts, setPosts] = useState([]);
  const [showPostCreator, setShowPostCreator] = useState(false);
  const { user: currentUser } = useContext(UserContext);

  useEffect(() => {
    fetch(`${API}/api/posts`)
      .then(res => res.json())
      .then(data => {
        const loadedPosts = data.map(post => ({
          ...post,
          images: JSON.parse(post.images || '[]'),
          likedBy: Array.isArray(post.likedBy)
            ? post.likedBy
            : JSON.parse(post.likedBy || '[]'),
          comments: JSON.parse(post.comments || '[]'),
          createdAt: new Date(post.created_at).toLocaleString(),
          author: post.author_name,
          authorAvatar: post.author_avatar,
        }));
        setPosts(loadedPosts);
      })
      .catch(err => console.error('Li ti bi vit:', err));
  }, []);

  const handlePostSubmit = async (newPost) => {
    if (!currentUser) return;

    const postToSave = {
      content: newPost.content,
      images: newPost.images,
      author: currentUser.name,
      authorId: currentUser.id,
      authorAvatar: currentUser.avatar,
    };

    try {
      console.log(' D liu gi ln:', postToSave);

      const res = await fetch(`${API}/api/posts`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(postToSave)
      });

      if (!res.ok) throw new Error('Li khi lu bi vit');

      const data = await res.json();

      const localPost = {
        ...postToSave,
        id: data.id,
        likes: 0,
        comments: [],
        likedBy: [],
        createdAt: new Date().toISOString()
      };

      setPosts(prev => [localPost, ...prev]);
      setShowPostCreator(false);
    } catch (err) {
      alert('Khng th ng bi. Li server.');
      console.error(err);
    }
  };

  const openPostCreator = () => {
    if (!currentUser) {
      alert('Vui lng ng nhp  ng bi');
      return;
    }
    setShowPostCreator(true);
  };

  return (
    <div className='news-container'>
      <div className='news-feed'>
        {currentUser && (
          <div className='create-post-box' onClick={openPostCreator}>
            <div className='user-avatar'>
              {currentUser.avatar ? (
                <img src={currentUser.avatar} alt='avatar' />
              ) : (
                <FaUserCircle className='default-avatar' />
              )}
            </div>
            <div className='create-post-input'>
              <span>{currentUser.name} i, bn ang ngh g th?</span>
            </div>
          </div>
        )}

        {showPostCreator && (
          <div className='modal-overlay' onClick={() => setShowPostCreator(false)}>
            <div onClick={(e) => e.stopPropagation()}>
              <PostCreator
                onPostSubmit={handlePostSubmit}
                onClose={() => setShowPostCreator(false)}
              />
            </div>
          </div>
        )}

        <div className='post-list'>
          {posts.map(post => (
            <Post
              key={post.id}
              post={post}
              setPosts={setPosts}
              currentUser={currentUser}
            />
          ))}
        </div>
      </div>
    </div>
  );
}

export default News;